import Vue from "vue";
import Router, { RouterOptions } from "vue-router";

Vue.use(Router);

const routeConfig: RouterOptions = {
  mode: "history",
  base: qTouch.basePath + qTouch.appPathMap.organization,
  routes: [
    {
      path: "/organizationCustomProps",
      name: "组织类型管理",
      component: () => import("../views/organizeTypeManage/OrganizationCustomProps.vue")
    },
    {
      path: "/organization_manage",
      name: "组织机构管理",
      component: () => import("../views/organizeManage/Organization.vue")
    }
  ]
};

export default routeConfig;
